<template>
  <div v-if="post">
    <h3>{{ post.title }}</h3>
    <div v-html="post.content"></div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      post: null,
    };
  },
  async created() {
    const { postId } = this.$route.params;
    const res = await this.$http.get(`./topic/${postId}`);
    this.post = res.data;
  },
};
</script>

